// chat window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-text-bubble {
    margin: 8px 0;
    $this: &;

    &__bubble-container {
        max-width: var(--chat-bubble-max-width);
        border-radius: var(--chat-bubble-radius);
        padding: 0.8rem 1.2rem;
        position: relative;
    }

    &__tail {
        position: absolute;
        display: block;
        width: 12px;
        height: 15px;
        bottom: 0;
    }

    &--from-me {
        @include eureka-flex-row-reverse;

        #{$this}__bubble-container {
            background-color: var(--chat-bubble-from-me-background-color);
            color: var(--chat-bubble-from-me-text-color);
        }

        #{$this}__tail {
            right: -6px;
            color: var(--chat-bubble-from-me-background-color);
        }
    }

    &--from-other {
        @include eureka-flex-row;

        #{$this}__bubble-container {
            background-color: var(--chat-bubble-from-other-background-color);
            color: var(--chat-bubble-from-other-text-color);
        }

        #{$this}__tail {
            left: -5px;
            color: var(--chat-bubble-from-other-background-color);
        }
    }
}
