@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

// chat window scss
.web-chat-window-container {
    max-width: 375px;
    height: 406px;
    $this: &;

    &__close-btn, &__minimize-btn {
        border-radius: 100%;
        background-color: var(--eureka-core-color-secondary-90);
        width: 24px;
        height: 24px;
        vertical-align: middle;
        color: var(--eureka-core-color-primary);
        border: none;

        .eureka-button__content-container {
            padding: 0;
        }

        .eureka-button__icon-container {
            padding: 0;
        }
    }

    &__title-btn-container--minimize-btn-container {
        margin-right: 16px;
    }

    .eureka-tile__heading-container {
        height: 44px;
        border-bottom: none;
        @include eureka-flex-row;
        #{$this}__title {
            flex: 1 0 auto;
            line-height: 2.4rem;
            font-weight: 600;
        }
    }
}
